.App {
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
  }
  
  .fetch-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
    transition: background-color 0.3s;
  }
  
  .fetch-button:hover {
    background-color: #0056b3;
  }
  
  .product-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .product-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
    width: 250px;
    padding: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: left;
  }
  
  .product-image {
    width: 100%; /* Ensures the image fills the width of the container */
    height: 150px; /* Fixed height */
    object-fit: cover; /* Ensures the image fits the container while maintaining its aspect ratio */
    border-radius: 10px;
    margin-bottom: 15px;
  }
  
  .product-name {
    font-size: 20px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .product-price {
    font-size: 18px;
    color: #28a745;
    font-weight: bold;
  }
  

  /* Cart Section */
.cart-container {
    margin-top: 30px;
    padding: 20px;
    background-color: #f8f9fa;
    border: 1px solid #ddd;
    border-radius: 10px;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
  }
  
  .cart-title {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .cart-item {
    display: flex;
    align-items: center;
    padding: 15px;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-bottom: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .cart-item-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 8px;
    margin-right: 15px;
  }
  
  .cart-item-details {
    flex: 1;
  }
  
  .cart-item-name {
    font-size: 18px;
    color: #333;
    margin-bottom: 5px;
  }
  
  .cart-item-price {
    font-size: 16px;
    color: #28a745;
    font-weight: bold;
  }
  
  .cart-empty {
    font-size: 18px;
    color: #666;
    text-align: center;
    margin-top: 20px;
  }
  